
import {
  defineComponent,
  ref,
  computed,
  onMounted,
} from "vue";
import { useRoute, useRouter } from 'vue-router'

import * as Connection from "../../src/connection";

import Page from "../a/page-component.vue";
import Card from "../a/card-component.vue";
import CardBody from "../a/card-body.vue";
import Btn from "../a/btn-component.vue";

export default defineComponent({
  components: {
    Page,
    Card,
    CardBody,
    Btn,
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const defferedType = ref("");
    const defferedTo = ref(0);

    const filterid = computed(() => {
      return route.params.filterid;
    });

    const midiinid = computed(() => {
      return route.params.midiinid;
    });

    const chainid = computed(() => {
      return route.params.chainid;
    });

    const midiRouteInput = computed(() => {
      return Connection.loginStatus.userDataConfig.getMidiRouteInput(
        Connection.loginStatus.inPorts[Number(midiinid.value)]
      );
    });

    const headrMsg = computed(() => {
      return `${midiinid.value}-${chainid.value} Add Schedule`;
    });

    const filterObj = computed(() => {
      return midiRouteInput.value.midiRouterChains[
        parseInt((chainid.value as string))
      ].getFilterSchedule(parseInt(filterid.value as string));
    });

    function doOk() {
      if (defferedType.value === "") {
        return;
      }

      if (filterid.value === "-1") {
        midiRouteInput.value.midiRouterChains[
          parseInt(chainid.value as string)
        ].addFilterSchedule(parseInt(defferedType.value), defferedTo.value);
      } else {
        filterObj.value.setVal(parseInt(defferedType.value), defferedTo.value);
      }
      router.push(`/midiin/${midiinid.value}`);
    }

    function doCancel() {
      if (filterid.value === "-1") {
        router.push(`/AddChainFilter/${midiinid.value}/${chainid.value}`);
      } else {
        router.push(`/midiin/${midiinid.value}`);
      }
    }

    onMounted(() => {
      if (filterid.value === "-1") {
        return;
      }
      defferedType.value = filterObj.value.defferedType.toString();
      defferedTo.value = filterObj.value.defferedTo;
    });

    return {
      doCancel,
      doOk,
      headrMsg,
      midiRouteInput,
      chainid,
      midiinid,
      filterid,
      defferedTo,
      defferedType,
    };
  },
});
